import { Injectable } from '@angular/core';
import { Http } from '@angular/http';
import { Estudante } from './estudante';
import { Endereco } from './endereco';
import { Mae } from './mae';

@Injectable({
  providedIn: 'root'
})
export class ApiService {

  estudantes:Estudante



  constructor(private http:Http) {

  }

  async getEstudantes():Promise<Array<Estudante>>{
      const response = await this.http.get('/estudantes')
      .toPromise();
    return response.json();
  }

  async getEstudanteData(id:number):Promise<Estudante>{
      const response = await this.http.get('/estudantes/estudante/'+id)
      .toPromise();
    return(response.json())
  }

  async getEnderecoData(id:number):Promise<Endereco>{
      const response = await this.http.get('/estudantes/endereco/'+id)
      .toPromise();
    return(response.json())
  }

  async getMaeData(id:number):Promise<Mae>{
      const response = await this.http.get('/estudantes/mae/'+id)
      .toPromise();
    return(response.json())
  }

  async delete(id:number):Promise<string>{
      const response = await this.http.get('/estudantes/delete/'+id)
      .toPromise();
    return(response.json())
  }


    async getCep(cep:string):Promise<string>{
        const response = await this.http.get('/cep/'+cep)
        .toPromise();
      return(response.json())
    }



}
