import { Component, OnInit} from '@angular/core';



import { Estudante } from '../estudante';
import { Endereco } from '../endereco';
import { Mae } from '../mae';


import { ActivatedRoute } from '@angular/router';

import { ApiService }  from '../api.service';

@Component({
  selector: 'ce-editar',
  templateUrl: './editar.component.html',
  styleUrls: ['./editar.component.css']
})


export class EditarComponent implements OnInit{

  estudante:Estudante;
  endereco:Endereco;
  mae:Mae;

  id:number
  cep:string

  cepValido:boolean
  cpfValido:boolean

  constructor(public apiServe:ApiService, private route: ActivatedRoute)  {
    this.cepValido = true
    this.cpfValido = true

  }


  ngOnInit() {
    this.getId();

    setInterval(() => { this.verificaCep(); }, 1000);

  }

  verificaCep(){
    if(this.cep != "vazio" && this.cep != '')
      this.cepValido = true;
  }

  getId(): void {
    this.id = +this.route.snapshot.paramMap.get('id');
    this.apiServe.getEstudanteData(this.id)
  .then((estudante:Estudante) => this.estudante = estudante);


    this.apiServe.getEnderecoData(this.id)
    .then((endereco:Endereco) => this.endereco = endereco)

    this.apiServe.getMaeData(this.id)
    .then((mae:Mae) => this.mae = mae);
  }

  public onBlurCpf(event: any): boolean {

        let cpf:string
        cpf = event.target.value;
        if (cpf == null) {
            this.cpfValido = false;
             return false;
        }
        if (cpf.length != 11) {
            this.cpfValido = false;
            return false;
        }
        if ((cpf == '00000000000') || (cpf == '11111111111') || (cpf == '22222222222') || (cpf == '33333333333') || (cpf == '44444444444') || (cpf == '55555555555') || (cpf == '66666666666') || (cpf == '77777777777') || (cpf == '88888888888') || (cpf == '99999999999')) {
            return false;
        }
        let numero: number = 0;
        let caracter: string = '';
        let numeros: string = '0123456789';
        let j: number = 10;
        let somatorio: number = 0;
        let resto: number = 0;
        let digito1: number = 0;
        let digito2: number = 0;
        let cpfAux: string = '';
        cpfAux = cpf.substring(0, 9);
        for (let i: number = 0; i < 9; i++) {
            caracter = cpfAux.charAt(i);
            if (numeros.search(caracter) == -1) {
                this.cpfValido = false;
                return false;
            }
            numero = Number(caracter);
            somatorio = somatorio + (numero * j);
            j--;
        }
        resto = somatorio % 11;
        digito1 = 11 - resto;
        if (digito1 > 9) {
            digito1 = 0;
        }
        j = 11;
        somatorio = 0;
        cpfAux = cpfAux + digito1;
        for (let i: number = 0; i < 10; i++) {
            caracter = cpfAux.charAt(i);
            numero = Number(caracter);
            somatorio = somatorio + (numero * j);
            j--;
        }
        resto = somatorio % 11;
        digito2 = 11 - resto;
        if (digito2 > 9) {
            digito2 = 0;
        }
        cpfAux = cpfAux + digito2;
        if (cpf != cpfAux) {
            this.cpfValido = false;
            return false;
        }
        else {
            this.cpfValido = true;
            return true;
        }
    }

  public onBlurCep(event: any): void{
    this.cep = "vazio"
    this.apiServe.getCep(event.target.value)
    .then((cep:string) => this.cep = cep);

    this.cepValido = true;
    if(this.cep == "vazio"){
      this.cepValido = false
    }

  }



  }
